<template>
  <div></div>
</template>

<script>
import Vue from "vue";
export default {
  data() {
    return {
      id: "",
    };
  },
  mounted() {
    this.$bus.$on("openWebSocket", () => {
      this.IfLogin();
    });
    this.IfLogin();
  },
  methods: {
    //判断登录页面断开websocket
    IfLogin() {
      if (
        this.$route.path != "/" &&
        this.$route.path != "/AssociationWindowSign"
      ) {
        if (localStorage.getItem("websocket") == null) {
          this.OpenWebSocket();
        }
      }
    },
    //建立websocket长连接
    OpenWebSocket() {
      this.id = localStorage.getItem("id");
      let websocket = null;
      if ("WebSocket" in window) {
        websocket = new WebSocket("ws://8.130.43.227:8085/ws/" + this.id);
      } else {
        this.$message.error({ message: "浏览器或此网站不支持WebSocket" });
      }
      websocket.onopen = () => {
        this.$axios.get("/User/LogInUserOne?id=" + this.id).then();
        console.log("连接成功");
      };
      websocket.onmessage = (e) => {
        //处理私聊请求
        if (
          e.data.substring(0, 49) ==
          "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj申请私聊"
        ) {
          this.$confirm("软件协会社员向您发来私聊请求", "软件协会")
            .then(() => {
              const message = {
                to: e.data.substring(49),
                content:
                  "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj同意私聊",
              };
              this.$websocket.send(JSON.stringify(message));
              this.$router.push({
                path: "/ChatBugHelp/UserChat",
                query: {
                  chatId: e.data.substring(49),
                },
              });
            })
            .catch(() => {
              const message = {
                to: e.data.substring(49),
                content:
                  "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj拒绝私聊",
              };
              this.$websocket.send(JSON.stringify(message));
            });
        }

        //处理同意和拒绝私聊请求
        if (
          e.data.substring(0, 49) ==
          "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj同意私聊"
        ) {
          this.$bus.$emit("closeLodingChatBugHelp");
          this.$router.push({
            path: "/ChatBugHelp/UserChat",
            query: {
              chatId: e.data.substring(49),
            },
          });
        } else if (
          e.data.substring(0, 49) ==
          "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj拒绝私聊"
        ) {
          this.$bus.$emit("closeLodingChatBugHelp");
          this.$message.warning({ message: "对方拒绝了您的私聊请求" });
        }

        //处理退出私聊
        if (
          e.data.substring(0, 49) ==
          "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj退出私聊"
        ) {
          this.$bus.$emit("closeUserChatBus");
        } else {
          //发送的消息进行推送
          this.$bus.$emit("UserChatBus", e.data);
        }
      };
      websocket.onclose = () => {
        console.log("连接关闭");
      };
      websocket.onerror = () => {
        console.log("连接错误");
      };
      Vue.prototype.$websocket = websocket;
    },
  },
  beforeDestroy() {
    this.$websocket.close();
  },
};
</script>

